<template>
  <div>
      <app-header></app-header>

      <b-container>
        <b-table striped
                :items="tests"
                :fields="testTableFields">
        </b-table>
      </b-container>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      testTableFields: [
        { key: 'title', label: 'Название' },
        { key: 'author.username', label: 'Автор' },
        { key: 'actions', label: 'Действия' }
      ]
    }
  },

  computed: {
    ...mapGetters({
      'tests': 'test/tests'
    })
  },

  mounted () {
    this.$store.dispatch('test/GET_TESTS')
  }
}
</script>



