import * as actions from './actions'
import getters from './getters'

const state = {
  tests: []
}

const mutations = {
  SET_TESTS: (state, list) => { state.tests = list },

  ADD_TEST: (state, test) => { state.tests.push(test) }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}



